/* Стили для раздела "Образование" */
.education {
    background-color: var(--coral);
    padding: 0px 0; /* Отступы сверху и снизу */
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .education-content {
    max-width: 1200px; /* Фиксированная ширина для контента */
    margin: 0 auto; /* Центрирование контента */
    padding: 20px; /* Отступы внутри блока */
    display: flex; /* Используем Flexbox для разделения на две части */
  }
  
  .education-title-1 {
    font-size: 50px; /* Размер текста кнопки */
    flex: 1; /* Занимает вторую половину ширины */
    transform: translateY(40%); /* Центрирование текста по вертикали */
  }
  
  .education-title-1 h2 {
    font-size: 50px;
  }
  
  .education-text {
    flex: 1; /* Занимает половину ширины */
    color: var(--white); /* Черный текст */
    text-align: left; /* Выравнивание текста по левому краю */
  }
  
  .education-text p {
    font-size: 28px;
    margin-top:20px;
    margin-bottom: 10px;
    font-weight: 600;
    
  }
  
  .education-text ul {
    list-style-position: outside; /* Устанавливаем позицию маркеров вне блока */
    padding-left: 20px; /* Устанавливаем отступ для списка */
    margin: 0; /* Убираем отступы у списка */
  }
  
  .education-text li {
    margin-left: 0; /* Убираем отступ слева для элементов списка */
    padding-left: 0; /* Убираем отступ для текста рядом с маркером */
    font-size: 24px;
    line-height: 1.5; /* Увеличиваем межстрочный интервал для лучшего выравнивания */
  }
  
  .education-title-1 {
    flex: 1;
    color: var(--blue);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top:10px;
    
  }
  
  .education-title-1 h2 {
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 800;
    font-size: 70px; 
  }