.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    background-color: #111;
    color: #fff;
    font-family: 'Arial', sans-serif;
  }
  
  .footer-left {
    font-size: 18px;
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 300;
    margin-left:30px;
  }
  .footer-right {
    font-size: 18px;
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 300;
    margin-right: 30px;
  }
  
  .footer-right a {
    color: #ffffff;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .footer-right a:hover {
    color: #acacac;
  }