.contact {
    padding: 40px 0;
    background-color: rgb(255, 255, 255);
  }
  
  .contact-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  
  .contact-left {
    width: 48%;
  }
  
  .contact-right {
    width: 48%;
    align-items: center;
    justify-content: center;
  }
  
  #yandex-map {
    width: 100%;
    height: 400px; /* Устанавливаем желаемую высоту карты */
    border-radius: 25px; /* Добавляем скругленные углы */
    overflow: hidden; /* Чтобы содержимое не выходило за границы */
}

  
  .contact-left h6 {
    margin-left: 10px;
    text-align: center;
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 600;
    font-size: 70px; 
    color: rgb(210, 99, 89);
    margin-bottom: 50px;
  }
  
  .contact-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .contact-item img {
    width: 25px;
    height: 25px;
    margin: 0px 15px 0px 25px;
  }
  
  .contact-link {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: inherit;
    transition: color 0.05s;
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 500;
    font-size: 40px; 
  }
 
  
  .contact-link:hover {
    color: #999999; /* Цвет при наведении */
  }
  
 
  
  .contact-item span {
    font-size: 30px;
    transition: transform 0.05s, color 0.05s ease-out;
  }
  