/* ABOUT */
/* Стили для раздела "Обо мне" */
.about {
    background-color: rgb(255, 255, 255);
    padding: 0px 0; /* Отступы сверху и снизу */
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .about-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    background-color: rgb(255, 255, 255);
    align-items: center; /* Центрируем элементы по вертикали */
  }
  
  .about-text {
    flex: 1; /* Занимает половину ширины */
    color: black; /* Черный текст */
    margin-top: 30px;
  }
  .about-text p{
    font-size: 24px;
    text-align: left; /* Выравнивание текста по левому краю */
  }
  .about-title-1 {
    
    flex: 1;
    color: rgb(210, 99, 89);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 0px;
    padding-top:40px;
  }
  .about-title-1 h2{
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 300;
    font-size: 70px;
    
  }