/* ABOUT */
/* Стили для раздела "Обо мне" */
.directions {
    background-color: rgb(255, 255, 255);
    padding: 0px 0; /* Отступы сверху и снизу */
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
    
  }
  
  .directions-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    flex-direction: column; /* Располагаем карточки по вертикали */
    align-items: center; /* Центрируем карточки по горизонтали */
    background-color: rgb(255, 255, 255);
  }
  
  .directions-text {
    flex: 1; /* Занимает половину ширины */
    color: black; /* Черный текст */
    margin-top: 0px;
  }
  .directions-text p{
    font-size: 24px;
  }
  .directions-title-1 {
    flex: 1;
    color: rgb(210, 99, 89);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 0px;
    padding-top:10px;
    margin-bottom: 10px;
  }
  .directions-title-1 h2{
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 700;
    font-size: 70px; 
  }