/* ABOUT */
.biography {
  background-color: rgb(255, 255, 255);
  padding: 0px 0;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.biography-content {
  max-width: 1200px;
  margin: 20px auto;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: rgb(255, 255, 255);
}

.biography-text {
  width: 50%;
  color: black;
  padding-right: 0px;
  margin-top: 30px;
  position: relative;
  max-height: 700px;
  overflow: hidden;
  transition: max-height 0.8s ease-in-out;
  display: flex;
  flex-direction: column;
}

.biography-text.expanded {
  max-height: 6200px;
}

.biography-text h1 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 700;
  font-size: 70px; 
  color: var(--coral);
  margin: 20px 0 50px 0;
}

.biography-text p {
  font-size: 24px;
  text-align: left;
  margin: 10px 0px 0px 10px
}

.text-container {
  overflow: hidden;
}

.biography-img {
  width: 48%;
}

.biography-image {
  max-width: 100%;
  height: auto;
  border-radius: 30px;
}




/* Кнопка с заливкой */
.toggle-button {
  margin-top: 20px;
  padding: 10px 20px;
  color: black;
  background-color: transparent;
  border: 1px solid black;
  border-radius: 50px;
  cursor: pointer;
  font-size: 20px;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  position: relative;
  z-index: 1;
  transition: color 0.4s ease, background-color 0.4s ease;
}

/* При наведении: заливка кнопки цветом */
.toggle-button:hover {
  color: white;
  background-color: var(--coral);
  border-color: var(--white);
}





