.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  max-width: 1200px; /* Немного меньше полной ширины */
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.3); /* Полупрозрачный фон */
  backdrop-filter: blur(10px); /* Эффект размытия */
  padding: 1rem;
  z-index: 1000; /* Чтобы навбар всегда был сверху */
  border-radius: 20px;
  transition: transform 0.3s ease-in-out; /* Плавный переход */
  margin-top: 10px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.hidden {
  transform: translateY(-120%); /* Скрыть навбар вверх */
}

.visible {
  transform: translateY(0); /* Показать навбар */
}
  
  /* Логотип слева */
  .navbar-left {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-left:12px;
  }
  
  .navbar-logo {
    width: 50px;
    border-radius: 50%;
    transition: transform 0.3s ease;
  }
  .navbar-logo:hover {
    transform: scale(1.1) ; /* Увеличение при наведении */
  }
  
  /* Ссылки по центру */
  .navbar-center {
    flex-grow: 1;
    display: flex;
    justify-content: center;
  }
  
  .navbar-center ul {
    list-style: none;
    display: flex;
    gap: 20px; /* Отступы между ссылками */
    margin: 0;
    padding: 0;
    cursor: pointer;
  }
  
  .navbar-center ul li a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    font-size: 20px;
    padding: 8px;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
    transition: color 0.3s ease;
  }
  
  /* Цвет ссылки при наведении */
  .navbar-center ul li a:hover {
    color: #666666;
  }
  
  /* Цвет активной ссылки */
  .navbar-center ul li a.active {
    color: #666666;
  }
  
  
  /* Номер телефона справа */
  .navbar-right {
    display: flex;
    align-items: center;
    gap: 10px; /* Расстояние между элементами */
    margin-right: 5px;
  }
  
  .navbar-right a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    font-size: 18px;
    padding: 8px;
    margin-top: 3px;
  }
  
  .navbar-right a:hover {
    color: #444444;
  }
  .navbar-logo-icon {
    width: 24px; /* Ширина иконок */
    height: 24px;
    margin-left: 10px; /* Отступ слева от номера телефона */
    transition: transform 0.3s ease;
  }
  
  .navbar-logo-icon:hover {
    transform: scale(1.1); /* Увеличение при наведении */
  }