/* Стили для раздела "Образование" */
.notworking {
    background-color: var(--blue);
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
    padding-top: 100px;
  }
  
  .notworking-content {
    max-width: 1200px; /* Фиксированная ширина для контента */
    margin: 0 auto; /* Центрирование контента */
    padding: 20px; /* Отступы внутри блока */
    display: flex; /* Используем Flexbox для разделения на две части */
  }
  
  .notworking-title-1 {
    font-size: 80px;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top:10px;
  }
  
  .notworking-title-1 h2 {
    font-size: 10px;
    font-family: "Dela Gothic One", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .notworking-text {
    flex: 1; /* Занимает половину ширины */
    color: black; /* Черный текст */
    text-align: left; /* Выравнивание текста по левому краю */
    
  }
  
  
  .notworking-text p{
    margin-top: 0px;
    font-family: "Raleway", sans-serif;
    font-weight:400;
    font-style: normal;
    font-size: 24px;
    color:var(--white);
    
  }
  
  .notworking-title-1 {
    flex: 1; /* Занимает вторую половину ширины */
    color: var(--coral);
    right: 5%; /* Расположение текста немного отступает от правого края */
    transform: translateY(40%); /* Центрирование текста по вертикали */
  }
  
  .notworking-title-1 h2 {
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 600;
    font-size: 70px; 
  }