.mail {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    background-color: var(--white);
}
  
.container {
    display: flex;
    flex-wrap: wrap;
    max-width: 1200px;
    width: 100%;
    justify-content: space-between;
    margin: 20px auto;
}
  
.image-container {
    flex: 1;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.image-container img {
    max-width: 100%;
    height: auto;
    border-radius: 30px;
}
  
.form-container {
    flex: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
  
.form-container h8 {
    margin-bottom: 70px;
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 500;
    font-size: 70px; 
    color: var(--coral);
}
  
.form-container h9 {
    margin-bottom: 30px;
    font-family: "Raleway", sans-serif;
    font-weight: 300;
    font-style: normal;
    font-size:24px;
    text-align: left;
}
  
form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
}
  
label {
    margin-bottom:15px;
    font-size: 18px;
    text-align: start;
    font-family: "Raleway", sans-serif;
    font-weight: 300;
    font-style: normal;
}
  
input {
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 20px;
    width: 100%;
    height: 42px;
}
  
button {
    align-self: center;
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 15px;
    margin-top: 20px;
    background-color: #ffffff;
    color: rgb(0, 0, 0);
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    border: 1px solid #ccc;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
  
button:hover {
    background-color: #dbdbdb;
}
  
button.success {
    background-color: #28a745;
    color: white;
    transition: background-color 0.3s ease;
}
  
button.error {
    background-color: #dc3545;
    color: white;
    transition: background-color 0.3s ease;
}
  
.checkmark {
    color: white;
    font-size: 17px;
}
