.fee-container {
  position: relative;
  padding: 20px;
  background-color: rgb(255, 255, 255);
}

.header-text-huy  {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: rgb(210, 99, 89);
}
.header-text-huy h2 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 500;
  font-size: 70px; 
  margin-bottom: 205px;
}

.cards-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 20px; /* для отступа от текста */
  background-color: rgb(255, 255, 255);
  margin-right: 70px;
  
}

.fee-card {
  margin-top: 120px;
  margin-right: 20px;
  width: 428px;
  height: 600px;
  background: #f0f0f0;
  border-radius: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-text-wrapper {
  position: absolute;
  bottom: 0px;
  left: 0;
  right: 0;
  
  text-align: center;
  color: #fff;
  
}

.card-text {
  
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px;
  border-radius: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 28px;
}
