.consultation {
  background-color: var(--white);
  padding: 10px 0;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.consultation h2 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 600;
  font-size: 70px; 
  color: var(--blue);
  margin-top: 10px;
}
.consultation p {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size:18px;
  font-style: normal;
}
.consultation p1 {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size:30px;
  font-style: normal;
  color:var(--blue);
}
.consultation p2 {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size:18px;
  font-style: normal;
  color:var(--blue);
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: 20px auto;
  min-height: 60vh;
  align-items: stretch; /* У всех карточек одинаковая высота */
}

.card {
  background-color: var(--grey);
  margin: 8px;
  padding: 5px;
  border-radius: 8px;
  width: calc(33.33% - 20px);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Центрирование содержимого внутри карточки */
}

.card.visible {
  opacity: 1;
}

.card:last-child {
  width: calc(33.33% - 20px);
  margin: 10px auto;
}
