.guides {
    padding: 50px;
    background-color: #ffffff;
  }
  
  .guides-title {
    text-align: center;
    margin-bottom: 20px;
    font-family: 'TRIAL_Oceanic', sans-serif;
    font-weight: 400;
    font-size: 70px; 
    color:rgb(210, 99, 89);
  }
  .guides-title2 {
    text-align: center;
    margin-bottom: 40px;
    font-size: 24px; /* Увеличиваем размер заголовка */
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .guides-list {
    display: flex;
    flex-direction: column; /* Располагаем карточки по вертикали */
    align-items: center; /* Центрируем карточки по горизонтали */
    gap: 30px; /* Расстояние между карточками */
  }
  
  .guide-item {
    position: relative;
    width: 550px; /* Увеличиваем ширину карточки */
    height: 800px; /* Увеличиваем высоту карточки */
    padding: 20px;
    background-color: var(--grey);
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .guide-content h33{
    font-size: 30px;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
    color:var(--blue);
  }
  
  .progress-bar {
    position: absolute;
    left: 0;
    top: 0;
    width: 6px; /* Увеличиваем ширину прогресс-бара */
    background-color: var(--blue);
    z-index: 1; /* Прогресс-бар будет поверх карточки */
  }
  
  .guide-image {
    width: 100%;
    height: 80%; /* Увеличиваем размер изображения */
    object-fit: cover;
    border-radius: 40px;
    z-index: 0;
  }
  
  .guide-content {
    margin-top: 20px;
    text-align: center;
    z-index: 0;
  }
  



  .download-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    padding: 12px 10px;
    background-color: var(--grey); /* Белый фон */
    color: var(--blue); /* Цвет текста черным */
    text-decoration: none;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-style: normal;
    border: 1px solid var(--blue); /* Черный контур кнопки */
    border-radius: 40px;
    font-size: 16px;
    
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: background-color 0.4s ease, color 0.4s ease, border-color 0.4s ease; /* Плавный переход */
  }
  
  .pdf-icon {
    filter: grayscale(); /* Иконка в серых тонах */
    width: 20px;
    height: 20px;
    margin-left: 10px; /* Отступ между иконкой и текстом */
    transition: filter 0.4s ease; /* Плавный переход фильтра */
  }
  
  .download-btn:hover {
    background-color: var(--blue); /* Черный фон */
    color: var(--white); /* Белый цвет текста */
    border-color: #000000; /* Белая граница */
  }
  
  .download-btn:hover .pdf-icon {
    filter: grayscale(0%); /* Иконка становится цветной */
  }
  
  
  



  